@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Inter-Regular.woff2") format("woff2"), url("/fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 400;
  src: url("/fonts/Inter-Italic.woff2") format("woff2"), url("/fonts/Inter-Italic.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/Inter-Medium.woff2") format("woff2"), url("/fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Inter-Bold.woff2") format("woff2"), url("/fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 700;
  src: url("/fonts/Inter-BoldItalic.woff2") format("woff2"), url("/fonts/Inter-BoldItalic.woff") format("woff"); }

.tabs {
  width: 100%;
  border-bottom: 1px solid #eef2f5;
  text-align: center; }

.tabs button {
  color: #939ba1;
  background: transparent;
  border: none;
  text-transform: uppercase;
  font-family: "Inter";
  height: 2.5rem;
  letter-spacing: 0.0125rem;
  display: inline-block;
  width: 130px;
  cursor: pointer; }

.tabs button.active:after {
  display: block;
  clear: both;
  content: '';
  position: relative;
  left: 0;
  bottom: -9px;
  height: 1px;
  width: 24px;
  border-bottom: 2px solid #4360df;
  margin: 0 auto; }

.tabs button.active {
  color: #4360df; }

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center; }

.footer button {
  background: #4360df;
  border-radius: 8px;
  color: #fff;
  margin: 0.625rem auto;
  border: none;
  font-family: "Inter";
  padding: 0.6875rem 2.375rem;
  font-size: 0.9375rem; }

.footer button:disabled,
.footer button[disabled] {
  background: #939ba1; }

.footer .disclaimer {
  font-size: 0.9375rem;
  color: #939ba1;
  line-height: 22px;
  font-family: "Inter";
  padding: 1rem;
  border-bottom: 1px solid #eef2f5;
  margin: 0; }

.footer .disclaimer a {
  text-decoration: none;
  color: #4360df; }

.item,
.dapp {
  line-height: 22px;
  font-family: "Inter";
  font-size: 0.9375rem;
  display: block;
  height: 2rem;
  padding: 0 0.75rem; }

.item img,
.dapp img {
  margin-right: 0.75rem;
  vertical-align: middle; }

.dapp {
  margin-bottom: 0.75rem;
  font-size: 1.0625rem;
  font-weight: 500; }

.dapp img {
  vertical-align: bottom; }

.badge, .greenBadge, .redBadge {
  float: right;
  border-radius: 24px;
  color: #ffffff;
  font-family: "Inter";
  font-size: 0.9375rem;
  margin-right: 1rem;
  padding: 0.1875rem 0.625rem; }

.greenBadge {
  background: #44d058; }

.redBadge {
  background: #f00; }

.inputArea {
  text-align: center;
  width: 300px;
  position: fixed;
  top: 40%; }

.inputArea input {
  margin: auto;
  width: 50%;
  border: none;
  border-bottom: 1px solid #eef2f5;
  text-align: center;
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter"; }

.inputArea input:focus {
  outline: none; }

.inputArea::after {
  position: absolute;
  top: 5px;
  right: 0;
  transition: all 0.05s ease-in-out;
  content: 'SNT';
  color: #939ba1;
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter"; }

.inputArea span {
  font-size: 2rem;
  line-height: 1.75rem;
  font-family: "Inter";
  position: relative;
  top: 5px; }

.image {
  max-width: 2.5rem;
  max-height: 2.5rem;
  margin-top: 0.9375rem;
  margin-right: 1rem; }
